.card {
  display: inline-block;
}

.card-stack {
  height: 190px;
  position: relative;
  margin-bottom: 10px;
}

.card-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 3px;
}

.card-title {
  font-size: 16px;
  line-height: 16px;
  color: #252728;
  margin-bottom: 5px;
}

.card-price-block {
  display: flex;
  gap: 10px;
  align-items: flex-start;
}

.card-price {
  font-size: 16px;
  color: #f25e7f;
}
.card-discount {
  text-decoration: line-through;
  font-size: 12px;
  color: #969caf;
}
