.filter {
  overflow: auto;
  padding: 20px;
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 1000;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.filter-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
}

.filter h4 {
  margin-bottom: 10px;
  font-weight: 300;
  font-size: 16px;
}

.filter-group {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.filter-item {
  margin-bottom: 20px;
}

@media (min-width: 500px) {
  .filter {
    width: 40%;
    right: 0;
  }
}
@media (min-width: 500px) and (max-width: 700px) {
  .filter {
    width: 60%;
  }
}
